import React, { useState, useEffect } from 'react';
import { Line } from 'react-chartjs-2';
import { Chart as ChartJS, CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend } from 'chart.js';
import axios from "axios";
import { useSearchParams } from 'react-router-dom';

ChartJS.register(CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend);

function BMICalculator() {
    const [weight, setWeight] = useState("");
    const [height, setHeight] = useState("");
    const [bmi, setBmi] = useState(null);
    const [weightHistory, setWeightHistory] = useState([]);
    const [noWeightMessage, setNoWeightMessage] = useState(false); // add state for no weight message
    const [searchParams] = useSearchParams();  
    const actualUserId = searchParams.get('userId') || 'default_name'; // URL  userId  
    console.log('User ID:', actualUserId);  //   

    useEffect(() => {
        updateWeightHistory();  //      
    }, []);
    
    const calculateBMI = async () => {
        if (weight && height) {
            const heightInMeters = height / 100;
            const bmiValue = weight / (heightInMeters * heightInMeters);
            setBmi(parseFloat(bmiValue.toFixed(2)));
    
            const today = new Date();
            today.setHours(today.getHours() + 9); //   
    
            const formattedDate = today.toISOString().slice(0, 19).replace('T', ' '); // 'YYYY-MM-DD HH:MM:SS' 
    
            try {
                const response = await fetch(`http://223.194.154.149:5001/api/bmi?userId=${actualUserId}`, {
                    method: "POST",
                    headers: { "Content-Type": "application/json" },
                    body: JSON.stringify({
                        date: formattedDate,
                        weight: weight,
                        userId: actualUserId,
                    }),
                });
                
                const data = await response.json();
    
                if (data.success) {
                    console.log("   !");
                    updateWeightHistory();  //   
                    setWeight("");  //  
                    setBmi(null);    // BMI  
                } else {
                    console.error("   :", data.message);
                }
            } catch (error) {
                console.error("     :", error);
            }
        }
    };    
    
    const updateWeightHistory = async () => {
        try {
            const response = await axios.get("http://223.194.154.149:5001/api/bmi");
            const data = response.data.map(entry => {
                const utcTime = new Date(entry.date); // UTC  
                const korTime = new Date(utcTime);
                korTime.setHours(korTime.getHours() + 9); //   
    
                return {
                    ...entry,
                    date: korTime.toISOString(),  // 'YYYY-MM-DDTHH:MM:SS.sssZ' 
                };
            });
            setWeightHistory(data);  //     
        } catch (error) {
            console.error("   :", error);
        }
    };

    const handleDeleteWeight = async (record) => {
        const { date, weight } = record;
        const userIdToDelete = actualUserId || "default_name";  // actualUserId  "default_name" 
        
        console.log("  :", {
            date: date,
            weight: weight,
            userId: userIdToDelete
        });
    
        try {
            const formattedDate = new Date(date);  // ISO  
            formattedDate.setHours(formattedDate.getHours() - 9);  //   
    
            // 'YYYY-MM-DD HH:MM:SS'  
            const year = formattedDate.getFullYear();
            const month = formattedDate.getMonth() + 1; //  0  +1                
            const day = formattedDate.getDate();
            const hours = formattedDate.getHours();
            const minutes = formattedDate.getMinutes();
            const seconds = formattedDate.getSeconds();
    
            const deleteDate = `${year}-${month < 10 ? '0' + month : month}-${day < 10 ? '0' + day : day} ${hours < 10 ? '0' + hours : hours}:${minutes < 10 ? '0' + minutes : minutes}:${seconds < 10 ? '0' + seconds : seconds}`;
    
            //   
            const response = await fetch("http://223.194.154.149:5001/api/bmi", {
                method: "DELETE",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify({
                    date: deleteDate,
                    weight: weight,
                    userId: userIdToDelete,  //   userId 
                }),
            });
    
            const data = await response.json();
    
            if (data.success) {
                console.log(data.message); //    
                setWeightHistory(prevHistory => {
                    const updatedHistory = prevHistory.filter(entry => entry.date !== deleteDate);
    
                    if (updatedHistory.length === 0) {
                        setNoWeightMessage(true);
                    }
    
                    return updatedHistory;
                });
    
                await updateWeightHistory();
            } else {
                console.error("   :", data.message);
            }
        } catch (error) {
            console.error("   :", error.message);
        }
    };    

    const getRangeStyle = (min, max) => {
        const inRange = bmi !== null && bmi >= min && bmi < max;
        let color;
        if (min === 0) color = "#a3d8f4";
        else if (min === 18.5) color = "#b8e5b5";
        else if (min === 23) color = "#f7e0a1";
        else color = "#f9c0c0";

        return {
            backgroundColor: color,
            color: "#333",
            fontWeight: "bold",
            padding: "10px 0",
            flexGrow: 1,
            textAlign: "center",
            position: "relative",
            ...(inRange && {
                border: "3px solid black",
                borderRadius: "3px",
                boxShadow: "0 0 10px rgba(0, 0, 0, 0.2)",
            }),
        };
    };

    const getArrowStyle = (min, max) => {
        const inRange = bmi !== null && bmi >= min && bmi < max;
        if (inRange) {
            return {
                position: "absolute",
                top: "-120px",
                left: "50%",
                transform: "translateX(-50%)",
                fontWeight: "bold",
                fontSize: "120px",
                color: "red",
                zIndex: 10,
            };
        }
        return { display: "none" };
    };

    const chartData = {
        labels: weightHistory.map(entry => entry.date),
        datasets: [
            {
                label: '  ',
                data: weightHistory.map(entry => entry.weight),
                fill: false,
                borderColor: 'blue',
                tension: 0.1,
            },
        ],
    };

    return (
        <div style={{ padding: "20px" }}>
            <h2>BMI </h2>
            <input
                type="number"
                placeholder=" (kg)"
                value={weight}
                onChange={(e) => setWeight(e.target.value)}
                style={{ marginRight: "10px" }}
            />
            <input
                type="number"
                placeholder=" (cm)"
                value={height}
                onChange={(e) => setHeight(e.target.value)}
                style={{ marginRight: "10px" }}
            />
            <button onClick={calculateBMI}></button>
            {bmi && <p> BMI: {bmi}</p>}

            {/* BMI    */}
            <div style={{ marginTop: "20px" }}>
                <h3>BMI </h3>
                <div
                    style={{
                        display: "flex",
                        width: "100%",
                        borderRadius: "8px",
                        overflow: "hidden",
                        border: "1px solid #ddd",
                        position: "relative",
                    }}
                >
                    <div style={getRangeStyle(0, 18.5)}>
                        (0 - 18.5)
                        <div style={getArrowStyle(0, 18.5)}>&#8595;</div>
                    </div>
                    <div style={getRangeStyle(18.5, 23)}>
                         (18.5 - 23)
                        <div style={getArrowStyle(18.5, 23)}>&#8595;</div>
                    </div>
                    <div style={getRangeStyle(23, 25)}>
                         (23 - 25)
                        <div style={getArrowStyle(23, 25)}>&#8595;</div>
                    </div>
                    <div style={getRangeStyle(25, 100)}>
                         (25 - 100)
                        <div style={getArrowStyle(25, 100)}>&#8595;</div>
                    </div>
                </div>
            </div>

            {/*     */}
            <div style={{ marginTop: "20px" }}>
                <h3>  </h3>
                <Line data={chartData} />
            </div>

            {/*   */}
            <div style={{ marginTop: "20px" }}>
                <h3> </h3>
                {noWeightMessage && <p>   .</p>}
                <ul>
                    {weightHistory.map((record, index) => (
                        <li key={index}>
                            {record.date} - {record.weight}kg{" "}
                            <button onClick={() => handleDeleteWeight(record)}></button>
                        </li>
                    ))}
                </ul>
            </div>
        </div>
    );
}

export default BMICalculator;
